/* region page*/
.page {
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
  line-height: 22px;
  scroll-behavior: smooth;
  color: #fff;
}

.page__body {
  margin: 0;
}

.page__menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;

  opacity: 0;
  pointer-events: none;
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
}

/* #endregion*/
/* #region header*/
.header {
  box-sizing: border-box;
  height: 100vh;
  background-image: url("../images/header-bg-mobile.png");
  background-size: cover;
  background-position: center;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* #endregion*/
.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  display: flex;
  gap: 24px;
}

.top-bar__logo {
  display: flex;
}

/* #region menu*/
.menu {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  box-sizing: border-box;

  height: 100vh;
  background-color: #c2c2c2;
  opacity: 0;
  pointer-events: none;
  font-weight: 600;
  text-transform: uppercase;
}

.menu__top {
  margin-bottom: 24px;
}

.menu__nav {
  margin-bottom: 40px;
}

/* #endregion*/
.menu__content {
  padding-inline: 20px;
}

.menu__phone-number {
  color: inherit;
  text-decoration: none;
  display: block;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
}

.menu__book-a-test-ride {
  display: block;
  padding-bottom: 7px;
  border-bottom: 1px #1d1d1d;
  width: fit-content;

  color: inherit;
  text-decoration: none;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
}

.nav__link {
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.header__bottom {
  padding: 0 20px 88px;
  display: flex;
  justify-content: center;
}

.header__title {
  margin: 0;
  font-family: Poppins;
  font-size: 36px;
  font-weight: 700;
  line-height: 36px;
  letter-spacing: -1.5px;
  text-align: center;
  color: #fff;
}

/* #region icon*/
.icon {
  height: 24px;
  width: 24px;
  display: block;
}

.icon--phone {
  background-image: url("../icons/icon-phone.png");
}

.icon--menu {
  background-image: url("../icons/icon-burger-menu.png");
}

.icon--close {
  background-image: url("../icons/icon-close.png");
}

/* #endregion*/

/* #region footer*/
.footer {
}
/* #endregion*/
